.App {
  height: 95px;
  width: 51px;
  margin: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.circle {
  height: 36px;
  width: 36px;
  background-color: #f7f0de;
  border-radius: 36px;
}
.input {
  height: 15px;
  width: 55px;
  border: none;
  outline: none;
  color: #7e658d;
  background-color: #f7f0de;
}